.input {
  margin: 0;
  width: 100%;
  height: 60px;
  margin-top: 25px;
  padding: 0 12px;
  display: flex;
  cursor: text;
  align-items: center;
  justify-content: space-between;
  box-sizing: border-box;
  background-color: #21212C;
  border: 2px solid #21212C;
  border-radius: 12px;
  transition: 0.3s;
  position: relative;
}

input[type="number"] {
  -moz-appearance: textfield;
  /* Для Firefox */
}

input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
  /* Для Chrome, Safari, Opera */
}

.input:-webkit-autofill {
  -webkit-box-shadow: 0 0 0 30px #21212C inset !important;
  -webkit-text-fill-color: #000 !important;
  color: #8585AD !important;
}

input:-internal-autofill-selected {
  -webkit-box-shadow: 0 0 0 30px #21212C inset !important;
  color:#8585AD  !important;
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active {
  -webkit-box-shadow: 0 0 0 30px #21212C inset !important;
}

.input_status_active {
  /*box-shadow: 0 0 5px #4C4CFF;*/
  border: 2px solid #21212C;
}

.input_status_error {
  border: 2px solid red;
}

.input_status_disabled {
  cursor: not-allowed;
}

.input_type_disabled {
  cursor: not-allowed;
}

.input__icon {
  cursor: text;
  line-height: 0;
}

.input__icon-action {
  cursor: pointer;
}

.input__icon-disabled {
  cursor: not-allowed;
}

.input__placeholder {
  display: block;
  position: absolute;
  cursor: text;
  top: 18px;
  text-align: left;
  color: #8585AD;
  font-size: 16px;
  line-height: 1.5;
  font-weight: normal;
  transition: 0.3s;
}

.input__placeholder-focused {
  top: 5px;
}

.input__placeholder-filled {
  top: 5px;
}

.input__textfield {
  display: block;
  cursor: text;
  background-color: transparent;
  border: none;
  width: 100%;
  color: white;
  margin-top: 16px !important;
  font-size: 16px;
  line-height: 1.5;
  font-weight: normal;
}

.input__textfield:-webkit-autofill {
  -webkit-text-fill-color: white !important;
}

.input__textfield:focus {
  outline: none;
}

.input__container {
  width: 100%;
  text-align: left;
  position: relative;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  overflow: hidden;
  clip: rect(0 0 0 0);
}

.input__placeholder-disabled {
  cursor: not-allowed;
}

.input__textfield-disabled {
  cursor: not-allowed;
  color: var(--text-inactive-color);
}

.input__textfield-disabled:-webkit-autofill {
  -webkit-text-fill-color: var(--text-inactive-color) !important;
}

.input__error {
  position: absolute;
  top: 73px;
  left: 12px;
  color: red;
}

.input__error:-webkit-autofill {
  -webkit-text-fill-color: var(--colors-interface-error) !important;
}

.input_size_default {
  width: 520px;
  min-height: 64px;
}

.input_size_small {
  width: 520px;
  min-height: 48px;
  padding-left: calc(var(--offset-base-variable) * 10);
  padding-right: calc(var(--offset-base-variable) * 10);
}